import React, { Component } from "react";
//import SearchForm from "./SearchForm";

class SearchResultContainer extends Component {
  state = {
    search: "",
    users: [],
    error: null,
    byLetters: true,
  };

  getUsers = (data) => {
    //console.log(data);
    this.setState({ users: data.results });
  };
  sortByName() {
    //const users = this.state;
    let newPostList = this.state.users;
    if (this.state.byLetters) {
      newPostList = this.state.users.sort(
        (a, b) => a.name.first > b.name.first
      );
    } else {
      newPostList = this.state.users.sort(
        (a, b) => a.name.first < b.name.first
      );
    }
    this.setState({
      byLetters: !this.state.byLetters,
      users: newPostList,
    });
  }
  componentDidMount() {
    fetch("https://randomuser.me/api/?results=10&nat=u")
      .then((res) => res.json())
      .then(this.getUsers)
      .catch();
  }
  /*  handleInputChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    this.setState({
      [name]: value,
    });
  }; */

  // When the form is submitted, search the Giphy API for `this.state.search`
  handleFormSubmit = (event) => {
    event.preventDefault();
  };

  render() {
    return (
      <div>
        <select>
          <option onClick={this.sortByName} value="albums">
            Name
          </option>
          <option value="members">Email</option>
          <option value="formed">DoB</option>
        </select>
        <table style={{ width: "100%" }}>
          {/* <SearchForm
            search={this.state.users}
            handleFormSubmit={this.handleFormSubmit}
            handleInputChange={this.handleInputChange}
          /> */}
          <button onClick={this.sortByName}>sort by name</button>
          <thead>
            <th>Photo</th>
            <th>Name</th>
            <th>Phone</th>
            <th>DoB</th>
            <th>Email</th>
          </thead>
          {this.state.users.length > 0 &&
            this.state.users.map((user) => (
              <tbody>
                <th>
                  <img src={user.picture.thumbnail} alt="EmployeeImage" />
                </th>
                <th>
                  {user.name.first} {user.name.last}
                </th>
                <th>{user.cell}</th>
                <th>{user.dob.date}</th>
                <th>{user.email}</th>Â 
              </tbody>
            ))}
        </table>
      </div>
    );
  }
}

export default SearchResultContainer;
